    /**
     * All errors of serial application pass through here
     * ```
     * try{ ... }
     * catch(e){ // OMG an error occurred
     *     errors(e);
     * }
     * ```
     * @author danidoble <Daniel Sandoval>
     * @function
     * @private
     * @param {object|string|errors} error Contains the information of the error
     * @return {void}
     */
    #errors(error) {

        if (document.getElementById(_id_vl_btn_open_modal_permissions) === null) {
            dd.error(error);
            dd.error("_id_vl_btn_open_modal_permissions doesn't exist in DOM");
            return;
        }

        if (error.toString().includes("Failed to execute 'requestPort' on 'Serial': Must be handling a user gesture to show a permission request")) {
            document.getElementById(window["_id_br" + this.#_no_machine + "_btn_connect_serial"]).classList.remove('hidden');
            document.getElementById(_id_vl_btn_open_modal_permissions).click();
            //dd.showDisconnectedMessage();
        } else if (error.toString().includes("The port is already open.")) {
            dd.debug(error);
            dd.warn(dd.__("Failed to open serial port. Trying again."));
            this.#withDevice();
            this.#serialDisconnect();
            setTimeout(async () => {
                this.#_port__cat += 1;
                await this.#serialConnect();
                /* !
                 ! this.#serialConnect().then((e) => {
                 !    if (e !== undefined) {
                 !        dd.log(e);
                 !    }
                 ! })
                 !*/
            }, 500);

            document.getElementById(_id_vl_btn_open_modal_permissions).click();
            //dd.showDisconnectedMessage();
        } else if (error.toString().includes("Cannot read properties of undefined (reading 'writable')")) {
            dd.warn(dd.__("The port isn't open."));
            this.#withoutDevice();
            this.#serialDisconnect();
            setTimeout(async () => {
                // try to connect
                await this.#serialConnect();
            }, 500);
        } else if (error.toString().includes("Cannot read properties of null (reading 'writable')")) {
            dd.warn(dd.__("The port isn't open."));
            this.#withoutDevice();
            this.#serialDisconnect();
            setTimeout(async () => {
                // try to connect
                await this.#serialConnect();
            }, 500);
        } else if (error.toString().includes("Failed to execute 'open' on 'SerialPort': A call to open() is already in progress.")) {
            dd.warn(dd.__("Failed to execute 'open' on 'SerialPort': A call to open() is already in progress."))
        } else if (error.toString().includes("The port is closed.")) {
            //alert("port is now closed")
            document.getElementById(_id_vl_btn_open_modal_permissions).click();
            //dd.showDisconnectedMessage();
        } else if (error.toString().includes("Select another port please")) {
            document.getElementById(window["_id_br" + this.#_no_machine + "_btn_connect_serial"]).classList.remove('hidden');
            document.getElementById(_id_vl_btn_open_modal_permissions).click();
            //dd.showDisconnectedMessage();
        } else if (error.toString().includes("No port selected by the user")) {
            document.getElementById(window["_id_br" + this.#_no_machine + "_btn_connect_serial"]).classList.remove('hidden');
            document.getElementById(_id_vl_btn_open_modal_permissions).click();
            //dd.showDisconnectedMessage();
        } else if (error.toString().includes("Cannot read property 'writable' of null")) {
            document.getElementById(window["_id_br" + this.#_no_machine + "_btn_connect_serial"]).classList.remove('hidden');
            document.getElementById(_id_vl_btn_open_modal_permissions).click();
            //dd.showDisconnectedMessage();
        } else if (error.toString().includes("Cannot read property 'writable' of undefined")) {
            document.getElementById(window["_id_br" + this.#_no_machine + "_btn_connect_serial"]).classList.remove('hidden');
            this.#withoutDevice();
            setTimeout(async () => {
                await this.initSerial();
            }, 100);
        } else if (error.toString().includes("The device has been lost")) {
            document.getElementById(window["_id_br" + this.#_no_machine + "_btn_connect_serial"]).classList.remove('hidden');
            this.#withoutDevice();
            //document.getElementById(_id_vl_btn_open_modal_permissions).click();
            ////dd.showDisconnectedMessage();
        } else if (error.toString().includes("Failed to open serial port")) {
            dd.warn(dd.__("Failed to open serial port. Trying again."));
            this.#withoutDevice();
            this.#serialDisconnect();
            setTimeout(async () => {
                // try to connect
                await this.#serialConnect();
                /* !
                ! this.#serialConnect().then((e) => {
                !    //when finish the connection
                !    dd.log(e);
                ! });
                ! */
            }, 500);
        } else if (error.toString().includes("navigator.serial is undefined")) {
            document.getElementById(this.#_serial_navigator_not_supported).classList.remove('hidden')
        } else if (error.toString().includes("This readable stream reader has been released and cannot be used to cancel its previous owner stream")) {
            document.getElementById(window["_id_br" + this.#_no_machine + "_btn_connect_serial"]).classList.remove('hidden');
            this.#withoutDevice();
            dd.checkDevicesConnected();
            this.extUnexpectedDisconnection(error)
        } else {
            dd.error(error)
        }
        //you can show all errors in console or just let unhandled errors
        //dd.error(error)
    }

