        dd.setDeviceStatus("boardroid", this.#_no_machine, connected)

        let lbl = dd.__('Connect');
        if (connected) {
            lbl = dd.__('Disconnect');
        } else {
            this.__connected_devices -= 1;
            dd.checkDevicesConnected();
        }
        if (this.#_serial_btn_connect_serial !== null) {
            for (let b = 0; b < this.#_serial_btn_connect_serial.length; b++) {
                this.#_serial_btn_connect_serial[b].textContent = lbl;
            }
        }
