    /**
     * Resolves the breaking lines of bytes input stream
     * ```
     * br1.#appendBuffer([0x12,0xFF]);
     * ```
     * @author danidoble <Daniel Sandoval>
     * @function
     * @private
     * @param {array|Uint8Array} arraybuffer input of stream device
     * @return {void}
     */
    #appendBuffer(arraybuffer) {
        if (arraybuffer !== undefined) {
            let tmp = new Uint8Array(this.#_serial_incoming.length + arraybuffer.byteLength);
            tmp.set(this.#_serial_incoming, 0);
            tmp.set(new Uint8Array(arraybuffer), this.#_serial_incoming.length);
            this.#_serial_incoming = tmp;
        }
    }

    /**
     * Gets a writer from the output stream and send the lines to the serial device
     * ```
     * br1.#writeToSerial([0x12,0xFF]);
     * ```
     * @author danidoble <Daniel Sandoval>
     * @function
     * @private
     * @param {array|Uint8Array|string} arr_bytes array of data to sent into device
     * @return {void}
     */
    #writeToSerial(arr_bytes) {
        try {
            if (!this.#_serial_text) {
                const bytes = new Uint8Array(arr_bytes);
                const writer = this.#_serial_port.writable.getWriter();
                writer.write(bytes).then((r) => {
                    if (r) {
                        dd.log(r);
                    }
                });
                writer.releaseLock();
            } else {
                const textEncoder = new TextEncoderStream();
                const writableStreamClosed = textEncoder.readable.pipeTo(this.#_serial_port.writable);
                const writer = textEncoder.writable.getWriter();
                writer.write(arr_bytes).then((r) => {
                    if (r) {
                        dd.log(r);
                    }
                });

                writableStreamClosed.then(r => {
                    dd.log(r);
                }).catch((e) => {
                    dd.error(e);
                })
                //writer.releaseLock();
            }
        } catch (err) {
            this.#errors(err);
        }
    }

