    /**
     * Sum hexadecimal values only for the boardroid, use sumHex function of Danidoble class for other sums
     * ```
     * br1.#sumHex(['1','B','DC'])
     * ```
     * @author danidoble <Daniel Sandoval>
     * @param {array|Uint8Array} arr
     * @private
     * @returns {string}
     */
    #sumHex(arr) {
        let sum = 0;
        arr.forEach((value, index) => {
            if (index !== 0 && index !== 11) {
                sum += parseInt(value, 16);
            }
        });
        return sum.toString(16);
    }

    /**
     * Get the type of response
     * ```
     * ... // your code before
     * br1.#getResponse(final_hex);
     * ```
     * @author danidoble <Daniel Sandoval>
     * @function
     * @private
     * @param {Number|Array} code
     * @param {null|object} data
     * @return {void}
     */
    #getResponse(code, data = null) {
        if (this.#_serial_text) {
            dd.warn("Modo texto habilitado", code)
        }
        if (code === 0) {
            this.#appMessage(data);
        } else if (code && code.length === 14) {
            this.#_connected = true;
            window["owl_br" + this.#_no_machine + "_connected"] = this.#_connected;
            if (this.#_interval_reconnection !== undefined) {
                clearInterval(this.#_interval_reconnection);
                this.#_interval_reconnection = undefined;
            }
            clearTimeout(this.#_timer_until_response);
            this.#_timer_until_response = undefined;
            this.#serialMessage(code);
        } else {
            this.#dividedCodeMessage(code, data);
        }
    }

