    /**
     * Execute engines matrix test for obviously testing
     * ```
     * br1.executeTestEnginesMatrix();
     * ```
     * @author danidoble <Daniel Sandoval>
     * @function
     * @param {boolean} [motors=false] if is true run test with 2 engines at same time
     * @return {void}
     */
    executeTestEnginesMatrix(motors = false) {
        let this1 = this;

        this1.#_percentage_test_matrix = (this1.#_aux_iteration - 10) * 100 / 79;
        if (this.default_alert) {
            if (this1.#_percentage_test_matrix >= 0 && this1.#_percentage_test_matrix < 100) {
                if (typeof Swal.fire === "function") {
                    if (!Swal.isVisible()) {
                        Swal.fire({
                            title: "¡En proceso!",
                            html: "¡Espera se está realizando una prueba de matriz!. Este mensaje se cerrará cuando termine.<br/><b></b>",
                            icon: "warning",
                            showCancelButton: false,
                            showConfirmButton: false,
                            didOpen: () => {
                                Swal.showLoading();
                                const b = Swal.getHtmlContainer().querySelector('b')
                                this1.#_private_timer_interval = setInterval(() => {
                                    b.textContent = this1.#_percentage_test_matrix.format(0, 3, ',', '.') + "%"
                                }, 100)
                            },
                            allowOutsideClick: false
                        });
                    }
                }
            } else if (this1.#_percentage_test_matrix >= 100) {
                if (typeof Swal.fire === "function" && Swal.isVisible()) {
                    clearInterval(this.#_private_timer_interval);
                    this.#_private_timer_interval = 0;
                    Swal.close()
                }
            }
        } else {
            this1.extPercentageTestMatrix(this1.#_percentage_test_matrix);
        }


        if (this1.#_aux_iteration === 90) {
            this1.#_aux_iteration = 10;
            return;
        }

        setTimeout(() => {
            let engine1 = dd.decToHex((this1.#_aux_iteration - 1).toString());
            let engine2 = "00";
            if (motors === true) {
                engine2 = dd.decToHex((this1.#_aux_iteration).toString());
            }
            setTimeout(() => {
                this1.dispense(engine1, engine2, "04");
            }, 500);
            if (this1.#_aux_iteration < 90) {
                this1.executeTestEnginesMatrix(motors);
            }
            this1.#_aux_iteration++;
        }, 1000)
    }

    /**
     * Count inserted money
     * ```
     * br1.countMoney();
     * ```
     * @author danidoble <Daniel Sandoval>
     * @function
     * @param {string} f_money type of money inserted
     * @param {string} f_where where go
     * @return {void}
     */
    countMoney(f_money, f_where) {
        let do_nothing = false;
        switch (f_money.toUpperCase()) {
            //coins
            case "G50":
                if (f_where === "tube") {
                    this.#_arr_coins.tubes.G50 += 1;
                } else {
                    this.#_arr_coins.box.G50 += 1;
                }
                this.#_arr_coins.G50 += 1;
                this.#_amount_inserted += .5;
                this.#_arr_coins.total += .5;
                break;
            case "C50":
                if (f_where === "tube") {
                    this.#_arr_coins.tubes.C50 += 1;
                } else {
                    this.#_arr_coins.box.C50 += 1;
                }
                this.#_arr_coins.C50 += 1;
                this.#_amount_inserted += .5;
                this.#_arr_coins.total += .5;
                break;
            case "P1":
                if (f_where === "tube") {
                    this.#_arr_coins.tubes.P1 += 1;
                } else {
                    this.#_arr_coins.box.P1 += 1;
                }
                this.#_arr_coins.P1 += 1;
                this.#_amount_inserted += 1;
                this.#_arr_coins.total += 1;
                break;
            case "P2":
                if (f_where === "tube") {
                    this.#_arr_coins.tubes.P2 += 1;
                } else {
                    this.#_arr_coins.box.P2 += 1;
                }
                this.#_arr_coins.P2 += 1;
                this.#_amount_inserted += 2;
                this.#_arr_coins.total += 2;
                break;
            case "P5":
                if (f_where === "tube") {
                    this.#_arr_coins.tubes.P5 += 1;
                } else {
                    this.#_arr_coins.box.P5 += 1;
                }
                this.#_arr_coins.P5 += 1;
                this.#_amount_inserted += 5;
                this.#_arr_coins.total += 5;
                break;
            case "P10":
                if (f_where === "tube") {
                    this.#_arr_coins.tubes.P10 += 1;
                } else {
                    this.#_arr_coins.box.P10 += 1;
                }
                this.#_arr_coins.P10 += 1;
                this.#_amount_inserted += 10;
                this.#_arr_coins.total += 10;
                break;
            //billetes
            case "P20":
                if (f_where === "stacker") {
                    this.#_arr_bills.stacker.P20 += 1;
                } else {
                    this.#_arr_bills.recycler.P20 += 1;
                }
                this.#_arr_bills.P20 += 1;
                this.#_amount_inserted += 20;
                this.#_arr_bills.total += 20;
                break;
            case "P50":
                if (f_where === "stacker") {
                    this.#_arr_bills.stacker.P50 += 1;
                } else {
                    this.#_arr_bills.recycler.P50 += 1;
                }
                this.#_arr_bills.P50 += 1;
                this.#_amount_inserted += 50;
                this.#_arr_bills.total += 50;
                break;
            case "P100":
                if (f_where === "stacker") {
                    this.#_arr_bills.stacker.P100 += 1;
                } else {
                    this.#_arr_bills.recycler.P100 += 1;
                }
                this.#_arr_bills.P100 += 1;
                this.#_amount_inserted += 100;
                this.#_arr_bills.total += 100;
                break;
            case "P200":
                if (f_where === "stacker") {
                    this.#_arr_bills.stacker.P200 += 1;
                } else {
                    this.#_arr_bills.recycler.P200 += 1;
                }
                this.#_arr_bills.P200 += 1;
                this.#_amount_inserted += 200;
                this.#_arr_bills.total += 200;
                break;
            case "P500":
                if (f_where === "stacker") {
                    this.#_arr_bills.stacker.P500 += 1;
                } else {
                    this.#_arr_bills.recycler.P500 += 1;
                }
                this.#_arr_bills.P500 += 1;
                this.#_amount_inserted += 500;
                this.#_arr_bills.total += 500;
                break;
            case "P1000":
                if (f_where === "stacker") {
                    this.#_arr_bills.stacker.P1000 += 1;
                } else {
                    this.#_arr_bills.recycler.P1000 += 1;
                }
                this.#_arr_bills.P1000 += 1;
                this.#_amount_inserted += 1000;
                this.#_arr_bills.total += 1000;
                break;
            case "R50":
                dd.log(dd.__("Dispensing bill"));
                if (f_where === "recyclerOut") {
                    this.#_arr_bills.recyclerOut.P50 += 1;
                }
                this.#_arr_bills.P50 -= 1;
                this.#_amount_retired += 50;
                this.#_arr_bills.total -= 50;
                break;

            default:
                do_nothing = true;
                // Don't sum to arrays
                break;
        }

        switch (f_money.toUpperCase()) {
            case "R20":
            case "R50":
            case "R100":
                this.extRetireBill();
                break;

            default:
                if (do_nothing) {
                    this.extCancelProcessByLever();
                } else {
                    this.extMoneySession();
                }
                break;
        }
        /**
         * The arrays of coins and bills save information of each session
         *
         * Uncomment line below to show in console the money of session
         */
        //dd.log(JSON.parse(JSON.stringify({"bills":this.#_arr_bills,"coins":this.#_arr_coins})));
    }

