    /**
     * Return change (money), this function only return change with the bill purse
     * ```
     * br1.returnChangeBillPurse();
     * ```
     * @author danidoble <Daniel Sandoval>
     * @function
     * @return {void}
     */
    returnChangeBillPurse() {
        let money = {
            "bills": {
                "b20": 0,
                "b50": 0,
                "b100": 0,
                "b200": 0,
                "b500": 0,
                "b1000": 0,
            }
        };
        let change_new = this._change;
        let notify_change_not_dispensed = false;
        if (this._change > 0) {
            let break_it = false;

            for (let i = 0; i < 80; i++) {
                // if change is 0 ends cycle for
                if (change_new === 0) {
                    break;
                }

                // banknotes
                if ((change_new - 1000) >= 0 && this.#_arr_bills.recycler.P1000 > money.bills.b1000) {
                    money.bills.b1000++;
                    money.bills.total_to_dispense += 1000;
                    change_new -= 1000;
                } else if ((change_new - 500) >= 0 && this.#_arr_bills.recycler.P500 > money.bills.b500) {
                    money.bills.b500++;
                    money.bills.total_to_dispense += 500;
                    change_new -= 500;
                } else if ((change_new - 200) >= 0 && this.#_arr_bills.recycler.P200 > money.bills.b200) {
                    money.bills.b200++;
                    money.bills.total_to_dispense += 200;
                    change_new -= 200;
                } else if ((change_new - 100) >= 0 && this.#_arr_bills.recycler.P100 > money.bills.b100) {
                    money.bills.b100++;
                    money.bills.total_to_dispense += 100;
                    change_new -= 100;
                } else if ((change_new - 50) >= 0 && this.#_arr_bills.recycler.P50 > money.bills.b50) {
                    money.bills.b50++;
                    money.bills.total_to_dispense += 50;
                    change_new -= 50;
                } else if ((change_new - 20) >= 0 && this.#_arr_bills.recycler.P20 > money.bills.b20) {
                    money.bills.b20++;
                    money.bills.total_to_dispense += 20;
                    change_new -= 20;
                }

                // reset the counter if the change doesn't complete
                if (i === 79 && change_new > 0) {
                    i = 0;
                    if (break_it) {
                        notify_change_not_dispensed = true;
                        break;
                    }
                    break_it = true;
                }
            }
        } else {
            // the change is in zeros, nothing is return
        }

        let str = JSON.stringify({
            "bills": {
                "p20": money.bills.b20,
                "p50": money.bills.b50,
                "p100": money.bills.b100,
                "p200": money.bills.b200,
                "p500": money.bills.b500,
                "p1000": money.bills.b1000
            },
            "change": this._change
        });

        let obj = JSON.parse(str);

        if (obj.change > 0) {
            // evaluate if banknotes will be dispensed
            let dispense_banknotes = false;
            let bills_quantity_amount = 0;
            Object.keys(obj.bills).forEach((value) => {
                if (!dispense_banknotes && obj.bills[value] > 0) {
                    if (value === "p20") {
                        bills_quantity_amount += 20 * obj.bills[value];
                    } else if (value === "p50") {
                        bills_quantity_amount += 50 * obj.bills[value];
                    } else if (value === "p100") {
                        bills_quantity_amount += 100 * obj.bills[value];
                    } else if (value === "p200") {
                        bills_quantity_amount += 200 * obj.bills[value];
                    } else if (value === "p500") {
                        bills_quantity_amount += 500 * obj.bills[value];
                    } else if (value === "p1000") {
                        bills_quantity_amount += 1000 * obj.bills[value];
                    }
                    dispense_banknotes = true;
                }
            });


            if (this._ict === true) {
                let quantity_x = 0;
                if (this._ict_bill === 0) {
                    quantity_x = (bills_quantity_amount / 20)
                } else if (this._ict_bill === 1) {
                    quantity_x = (bills_quantity_amount / 50)
                } else if (this._ict_bill === 2) {
                    quantity_x = (bills_quantity_amount / 100)
                } else if (this._ict_bill === 3) {
                    quantity_x = (bills_quantity_amount / 200)
                } else if (this._ict_bill === 4) {
                    quantity_x = (bills_quantity_amount / 500)
                } else if (this._ict_bill === 5) {
                    quantity_x = (bills_quantity_amount / 1000)
                }

                this.dispenseBanknotesICT(dd.decToHex((quantity_x).toString()));
                if (!dd._control) {
                    let currency_x = '50';
                    switch (this._ict_bill) {
                        case 0:
                            currency_x = '20';
                            break;
                        case 1:
                            currency_x = '50';
                            break;
                        case 2:
                            currency_x = '100';
                            break;
                        case 3:
                            currency_x = '200';
                            break;
                        case 4:
                            currency_x = '500';
                            break;
                        case 5:
                            currency_x = '1000';
                            break;
                    }
                }
            } else {
                this.dispenseBanknotes(
                    (obj.bills.p20).toString(16),
                    (obj.bills.p50).toString(16),
                    (obj.bills.p100).toString(16),
                    (obj.bills.p200).toString(16),
                    (obj.bills.p500).toString(16),
                    (obj.bills.p1000).toString(16)
                );
            }

        } else {
            // theres no change to return
        }
    }

    /**
     * Calc the change to dispense
     * ```javascript
     * br1.calcChange()
     * ```
     * @author danidoble <Daniel Sandoval>
     * @function
     * @return {{change: Number, error: number}|{error: (string|*)}}
     */
    calcChange() {
        if (this._price === undefined) {
            return {"error": dd.__("The price it is not defined, can not make a calc with a undefined value")};
        }
        if (this.#_amount_inserted === this._price) {// if mount is equal to price
            this._change = 0;
        } else if (this.#_amount_inserted > this._price) {// if mount is superior to price
            this._change = this.#_amount_inserted - this._price;
        } else { // if mount is minus to price
            return {"error": dd.__("The amount to be collected has not been covered")};
        }
        return {
            "change": this._change,
            "error": 0,
        };
    }

    /**
     * Return all money inserted by user in session
     * ```
     * br1.getBackInsertedMoney()
     * ```
     * @author danidoble <Daniel Sandoval>
     * @function
     * @param {string} [return_with='BOTH'] texts valid are: "BOTH", "COINS" and "BILLS".
     * @returns void
     */
    getBackInsertedMoney(return_with = 'BOTH') {
        if (this.__amount_inserted <= 0) {
            return;
        }
        let _change_ = this._change;
        this._change = this.__amount_inserted;
        if (return_with === "BOTH") {
            this.returnChange();
        } else if (return_with === "COINS") {
            this.returnChangeCoinPurse();
        } else if (return_with === "BILLS") {
            this.returnChangeBillPurse();
        } else {
            this._change = _change_;
        }
    }

