    startSenseDoor() {
        let this1 = this;
        this._log_time_door_open = [{
            "name": "open",
            "listen_since": (new Date()).getTime(),
            "listen_until": ((new Date()).getTime() + (30 * 1000))
        }];
        this._log_time_door_close = [{
            "name": "close",
            "listen_since": (new Date()).getTime(),
            "listen_until": ((new Date()).getTime() + (30 * 1000))
        }];
        this.#_interval_sense_door = setInterval(function () {
            this1.extBikeMessageElapsedTimeSenseDoor();
            this1._counter_sense_door++;
            if (this1._counter_sense_door > this1._limit_sense_door) {
                clearInterval(this1.#_interval_sense_door);
                this1.#_interval_sense_door = undefined;
                this1._counter_sense_door = 0;
                this1.extBikeCalc();
            }
        }, 1e3)
    }


