    /**
     * Start the waiting for sense
     * @function
     * @param {number} selection
     * @param {number} id_movement
     * @author danidoble <Daniel Sandoval>
     * @return void
     */
    waitingForSense(selection, id_movement) {
        this.__sense_selection = selection;
        this.__sense_id_movement = id_movement;
        this.#_waiting_for_sense = setInterval(() => {
            if (this.#_status_dispense === undefined) {
                dd.log("dispensando... #timeout:", this.#_count_timeout_sense + "/15");
                if (this.#_count_timeout_sense >= 15) {
                    this.#_count_timeout_sense = 0;
                    this.#_status_dispense = false;
                } else {
                    this.#_count_timeout_sense++;
                }
                this.extWaitingForSense('undefined');
            } else if (this.#_status_dispense) {
                dd.log("dispensing: " + this.#_status_dispense);
                this.clearSenseProduct();

                //user custom
                this.extWaitingForSense('dispensed');
            } else if (!this.#_status_dispense) {
                this.clearSenseProduct();

                //user custom
                this.extWaitingForSense('not-dispensed');
            }
        }, this.#_time_interval_sense);
    }

    /**
     * Start the waiting for sense, use this for testing
     * @function
     * @param {number} selection
     * @param {number} id_movement
     * @author danidoble <Daniel Sandoval>
     * @return void
     */
    waitingForSenseTest(selection, id_movement) {
        this.__sense_selection = selection;
        this.__sense_id_movement = id_movement;
        this.#_waiting_for_sense = setInterval(() => {
            if (this.#_status_dispense === undefined) {
                dd.log("dispensando... #timeout:", this.#_count_timeout_sense + "/15");
                if (this.#_count_timeout_sense >= 15) {
                    this.#_count_timeout_sense = 0;
                    this.#_status_dispense = false;
                } else {
                    this.#_count_timeout_sense++;
                }
                this.extWaitingForSenseTest('undefined');
            } else if (this.#_status_dispense) {
                dd.log("dispensing: " + this.#_status_dispense);
                this.clearSenseProduct();

                //user custom
                this.extWaitingForSenseTest('dispensed');
            } else if (!this.#_status_dispense) {
                this.clearSenseProduct();

                //user custom
                this.extWaitingForSenseTest('not-dispensed');
            }
        }, this.#_time_interval_sense);
    }

    /**
     * Check if has money to return
     * @function
     * @param {number} price_to_eval
     * @author danidoble <Daniel Sandoval>
     * @return boolean
     */
    hasToReturnChange(price_to_eval) {
        let totalMonedasTubos = (this.#_arr_coins.tubes.G50 * .5) +
            (this.#_arr_coins.tubes.P1) +
            (this.#_arr_coins.tubes.P2 * 2) +
            (this.#_arr_coins.tubes.P5 * 5) +
            (this.#_arr_coins.tubes.P10 * 10);
        let totalBilletesRecycler = (this.#_arr_bills.recycler.P20 * 20) + (this.#_arr_bills.recycler.P50 * 50);

        let money = {
            "coins": {"c50": 0, "p1": 0, "p2": 0, "p5": 0, "p10": 0},
            "bills": {"b20": 0, "b50": 0, "b100": 0, "b200": 0, "b500": 0, "b1000": 0, "total_to_dispense": 0}
        };

        let change_new = price_to_eval;
        let notifyChangeNotDispensed = false;
        if (price_to_eval > 0) {
            let break_it = false;
            for (let i = 0; i < 80; i++) {
                // if change is 0 ends cycle for
                if (change_new === 0) {
                    break;
                }
                // banknotes
                if ((change_new - 1000) >= 0 && this.#_arr_bills.recycler.P1000 > money.bills.b1000) {
                    money.bills.b1000++;
                    money.bills.total_to_dispense += 1000;
                    change_new -= 1000;
                } else if ((change_new - 500) >= 0 && this.#_arr_bills.recycler.P500 > money.bills.b500) {
                    money.bills.b500++;
                    money.bills.total_to_dispense += 500;
                    change_new -= 500;
                } else if ((change_new - 200) >= 0 && this.#_arr_bills.recycler.P200 > money.bills.b200) {
                    money.bills.b200++;
                    money.bills.total_to_dispense += 200;
                    change_new -= 200;
                } else if ((change_new - 100) >= 0 && this.#_arr_bills.recycler.P100 > money.bills.b100) {
                    money.bills.b100++;
                    money.bills.total_to_dispense += 100;
                    change_new -= 100;
                } else if ((change_new - 50) >= 0 && this.#_arr_bills.recycler.P50 > money.bills.b50) {
                    money.bills.b50++;
                    money.bills.total_to_dispense += 50;
                    change_new -= 50;
                } else if ((change_new - 20) >= 0 && this.#_arr_bills.recycler.P20 > money.bills.b20) {
                    money.bills.b20++;
                    money.bills.total_to_dispense += 20;
                    change_new -= 20;
                }
                // coins
                else if ((change_new - 10) >= 0 && this.#_arr_coins.tubes.P10 > money.coins.p10) {
                    money.coins.p10++;
                    change_new -= 10;
                } else if ((change_new - 5) >= 0 && this.#_arr_coins.tubes.P5 > money.coins.p5) {
                    money.coins.p5++;
                    change_new -= 5;
                } else if ((change_new - 2) >= 0 && this.#_arr_coins.tubes.P2 > money.coins.p2) {
                    money.coins.p2++;
                    change_new -= 2;
                } else if ((change_new - 1) >= 0 && this.#_arr_coins.tubes.P1 > money.coins.p1) {
                    money.coins.p1++;
                    change_new -= 1;
                } else if ((change_new - .5) >= 0 && this.#_arr_coins.tubes.G50 > money.coins.c50) {
                    money.coins.c50++;
                    change_new -= .5;
                }
                // reset the counter if the change doesn't complete
                if (i === 79 && change_new > 0) {
                    i = 0;
                    if (break_it) {
                        notifyChangeNotDispensed = true;
                        break;
                    }
                    break_it = true;
                }
            }
        } else {
            // the change is in zeros, nothing is return
        }
        return !((price_to_eval > (totalMonedasTubos) + totalBilletesRecycler) || (notifyChangeNotDispensed));

    }

