    /**
     * ```Customizable function``` <br>
     * Time elapsed since start sensing door
     * ```
     * br1.extBikeCalc()
     * ```
     * @author danidoble <Daniel Sandoval>
     * @function
     * @return void
     */
    extBikeCalc() {
        let _since_ = this._log_time_door_open[0].listen_since;
        let _average_ = 0.0;
        let _sum_ = 0;
        (this._log_time_door_open).forEach((val) => {
            if (!val.listen_since) {
                _average_ += (val.time);
                _sum_ += (val.time - _since_);
            }
        })
        dd.log(this._log_time_door_open)
    }

