    /**
     * ```Customizable function``` <br>
     * Custom this function to do some when sense is updated
     * @function
     * @param {string} _type_
     * @author danidoble <Daniel Sandoval>
     * @return void
     */
    extWaitingForSense(_type_) {
        switch (_type_) {
            case 'dispensed':
                dd.log('Result of dispense: "dispensed"');
                setTimeout(() => {
                    this.softReload();
                }, this.#_time_to_reload);
                break;
            case 'not-dispensed':
                dd.log('not-Result of dispense: "dispensed"');
                setTimeout(() => {
                    this.softReload();
                }, this.#_time_to_reload);
                break;
        }
    }

