import Danidoble from './class/danidoble/danidoble';
import Emulate from './class/danidoble/emulator';
import Boardroid from './class/webserial/boardroid';
import Jofemar from './class/webserial/jofemar';
import Locker from './class/webserial/locker';
import Relay from './class/webserial/relay';

let no_version = require('./../../package.json')['version'];
const dd = new Danidoble();

window.webserial = {
    Danidoble: Danidoble,
    Boardroid: Boardroid,
    Jofemar: Jofemar,
    Locker: Locker,
    Emulate: Emulate,
    Relay: Relay,
    __: dd.__,
    version: no_version,
};
window.__ = window.webserial.__;
/**
 * @deprecated
 */
window.webserial_version = window.webserial.version;
/**
 * @deprecated
 */
window.Danidoble = window.webserial.Danidoble;
/**
 * @deprecated
 */
window.Boardroid = window.webserial.Boardroid;
/**
 * @deprecated
 */
window.Jofemar = window.webserial.Jofemar;
/**
 * @deprecated
 */
window.Locker = window.webserial.Locker;
/**
 * @deprecated
 */
window.Emulate = window.webserial.Emulate;
/**
 * @deprecated
 */
window.Relay = window.webserial.Relay;

document.addEventListener("DOMContentLoaded", () => {
    console.log(dd.__("Webserial version [no_version] created by danidoble. (https://github.com/danidoble)").replace("[no_version]", window.webserial_version));
    dd.htmlData();
    let el = document.querySelector('body');
    el.append(window['__webserial_html__']);

    document.getElementById(window["_id_vl_btn_open_modal_permissions"]).addEventListener('click', async () => {
        document.getElementById(window["_id_vl_open_modal_permissions"]).classList.remove('hidden')
        dd.showDisconnectedMessage();
    })

    document.getElementById(window["_id_vl_btn_close_not_support"]).addEventListener('click', async () => {
        document.getElementById(window["_id_vl_not_support"]).classList.add('hidden')
    })

    if (window.location.protocol !== "https:") {
        if (typeof Swal.fire === "function") {
            Swal.fire({
                title: dd.__("Security breach!"),
                html: dd.__("Webserial needs 'HTTPS' to work. Also please upgrade your request to avoid security breaches."),
                icon: "error",
                showConfirmButton: true,
                showCancelButton: true,
                confirmButtonText: dd.__("Upgrade"),
                cancelButtonText: dd.__("Leave me alone"),
                allowOutsideClick: false
            }).then((result) => {
                if (result.isConfirmed) {
                    location.href = (location.href).replace("http://", "https://");
                } else {
                    console.error(dd.__("Security breach!"), dd.__("Webserial it will not work"))
                }
            });
        } else {
            let err = new Error();
            err.name = dd.__("Security breach!");
            err.message = dd.__("Webserial needs 'HTTPS' to work. Also please upgrade your request to avoid security breaches.");
            dd.error(err);
        }
    }

})