    /**
     * Make an error to see all trace of log, warn, error, and debug. This will be shown in debug or verbose option
     * console
     * ```
     * let data = 'your information to show in console debug'
     * let result = dd.#_cmakeLog_('log', data);
     * //result: {
     * //    "__trace__": [
     * //        "log: \"your information to show in console debug\"",
     * //        "at #_cmakeLog_ (https://localhost/webserial/dd/gf.js:73:21)",
     * //        "at <anonymous>:2:17"
     * //    ],
     * //    "info": "your information to show in console debug"
     * //}
     * ```
     * @author danidoble <Daniel Sandoval>
     * @function
     * @private
     * @param {string} type type of log (Log, Warn, Debug, Error, Your own custom name)
     * @param {array|string} data your data to show (object, array, string, number, etc.)
     * @returns {object}
     */
    #_cmakeLog_(type, data) {
        let r = null;
        let error = new Error();
        try {
            error.name = __(type.toLowerCase());
            error.message = JSON.stringify(data);
        } catch (e) {
            r = e;
        } finally {
            if (r === null) {
                r = error;
            }
            console.debug("_trace_real_", type + ": ", data, r);
        }
        let d = {
            __trace__: (r.stack.toString()).split("\n    "),
            info: data
        };
        this.#registerConsoleData(d);
        return d;
    }

    /**
     * Add to array data of console
     * ```
     * //dd.#registerConsoleData({
     * //    "__trace__": [
     * //        "log: \"your information to show in console debug\"",
     * //        "at #_cmakeLog_ (https://localhost/webserial/dd/gf.js:73:21)",
     * //        "at <anonymous>:2:17"
     * //    ],
     * //    "info": "your information to show in console debug"
     * //})
     * ```
     * @author danidoble <Daniel Sandoval>
     * @function
     * @private
     * @param {object} data
     * @param {boolean} force
     * @returns {void}
     */
    #registerConsoleData(data, force = false) {
        let date = new Date();
        let unix_time = date.getTime();//unix
        let day = dd.hexMaker(date.getDate());//day
        let year = date.getFullYear();//year
        let month = dd.hexMaker(date.getMonth() + 1);

        let hours = dd.hexMaker(date.getHours());
        let minutes = dd.hexMaker(date.getMinutes());
        let seconds = dd.hexMaker(date.getSeconds());
        let milliseconds = date.getMilliseconds();


        let host = new URL(window.location);

        let time_obj = {
            unix: unix_time,
            date: year + "-" + month + "-" + day,
            time: hours + ":" + minutes + ":" + seconds + "." + milliseconds,
            uri: host.href
        };

        let obj = {
            object: data,
            date: time_obj,
        };

        this.#console_data.push(obj);

        if (this.#console_data.length >= 300 || force === true) {
            this.extSaveConsole(JSON.stringify(this.#console_data), JSON.stringify(time_obj));
            this.#console_data = [];
        }
    }

    /**
     * Check if some text was written
     * ```
     * dd.onDD(() => {
     *  // put your function here
     *  // window.open('https://bit.ly/danidoble', '_blank');
     * });
     * ```
     * @author danidoble <Daniel Sandoval>
     * @function
     * @param {function} callback function to do after
     * @return {void}
     */
    onDD(callback) {
        let input = '';
        //const key = '686578736879667669'; //danidoble
        const key = 'danidoble';
        document.addEventListener('keydown', function (e) {
            //input += ("" + e.keyCode);
            input += ("" + e.key);
            if (input === key) {
                return callback();
            }
            if (!key.indexOf(input)) return;
            //input += ("" + e.keyCode);
            input = ("" + e.key);
        });
    }

