    /**
     * This function translate the content put inside of key
     * ``` javascript
     * dd.__("translate this")
     * //or
     * window.__ = dd.__;
     * __("translate this")
     * ```
     * @author danidoble <Daniel Sandoval>
     * @function
     * @param {string} key string in some language to translate to other language
     * @param {object} obj_of_lang your own abject with keys
     * @return {string|*}
     */
    __(key = "", obj_of_lang = null) {
        if (obj_of_lang !== null && typeof obj_of_lang === "object") {
            return (typeof obj_of_lang === "undefined" || !obj_of_lang[key]) ? key : obj_of_lang[key];
        }
        if (Danidoble.lang === "es") {
            return (typeof dd_lang === "undefined" || !dd_lang[key]) ? key : dd_lang[key];
        }
        return key;
    }

    /**
     * Parse decimal to hexadecimal
     * ```
     * dd.decToHex(11)
     * // result: 'B'
     * ```
     * @author danidoble <Daniel Sandoval>
     * @function
     * @param {string} val integer value
     * @return {string}
     */
    decToHex(val) {
        return parseInt(val).toString(16).toUpperCase();
    }

    /**
     * Parse hexadecimal value to decimal
     * ```
     * dd.hexToDec('FF')
     * // result: 255
     * ```
     * @author danidoble <Daniel Sandoval>
     * @function
     * @param {string} val hexadecimal value
     * @return {number}
     */
    hexToDec(val) {
        return parseInt(val, 16);
    }

    /**
     * Adds 0x before each value in array
     * ```
     * //Example 00 => 0x00
     *
     * dd.add0x(['04','FF','1'])
     * // result: ['0x04','0xFF','0x1']
     * ```
     * @author danidoble <Daniel Sandoval>
     * @function
     * @param {array} bytes array of bytes in hexadecimal without 0x preceding it
     * @return {Array}
     */
    add0x(bytes) {
        let new_bytes = [];
        bytes.forEach((value, index) => {
            new_bytes[index] = "0x" + value;
        });
        return new_bytes;
    }

    /**
     * Style hex data to make uniform and pretty
     * ``` javascript
     * dd.hexMaker('1')
     * //result: 01
     *
     * dd.hexMaker('0',3)
     * //result: 001
     * ```
     * @author danidoble <Daniel Sandoval>
     * @function
     * @param {string|number} val hexadecimal number
     * @param {number} [min=2] min length required of string
     * @returns {string}
     */
    hexMaker(val, min = 2) {
        if (val === undefined) {
            val = "00";
        }
        let missing = (min - (val.toString().length));
        if (missing > 0) {
            for (let i = 0; i < missing; i++) {
                val = "0" + val;
            }
        }
        return val.toString().toUpperCase();
    }

    /**
     * Parse an array of hex values to an array of dec values
     * ```
     * dd.arrDecToHex([10,11,12,13,14,15])
     * //result: ['A','B','C','D','E','F']
     * ```
     * @author danidoble <Daniel Sandoval>
     * @function
     * @param {array} arr Array of numbers in decimal
     * @returns {array}
     */
    arrDecToHex(arr) {
        let arr_new = [];
        arr.forEach((value, index) => {
            arr_new[index] = this.decToHex(value);
        });
        return arr_new;
    }

    /**
     * Parse an array of hexadecimal values to an array of decimal values
     * ```
     * dd.arrHexToDec(['A','B','C','D','E','F'])
     * //result: [10,11,12,13,14,15]
     * ```
     * @author danidoble <Daniel Sandoval>
     * @function
     * @param {array} arr Array of numbers in hexadecimal
     * @returns {array}
     */
    arrHexToDec(arr) {
        let arr_new = [];
        arr.forEach((value, index) => {
            arr_new[index] = this.decToHex(value);
        });
        return arr_new;
    }

    /**
     * Sum hexadecimal numbers
     * ```
     * dd.sumHex(['A','B','C','D','E','F'])
     * //result: '4b'
     * ```
     * @author danidoble <Daniel Sandoval>
     * @function
     * @param {array} arr Array of numbers in hexadecimal
     * @returns {string}
     */
    sumHex(arr) {
        //arr[1]+arr[2]+arr[3]+arr[4]+arr[5]+arr[6]+arr[7]+arr[8]+arr[9]+arr[10]...
        let sum = 0;
        arr.forEach((value) => {
            sum += parseInt(value, 16);
        });
        return sum.toString(16);
    }

    /**
     * Checksum of boardroid hexadecimal numbers
     * ```
     * dd.boardroidSumHex(['A','B','C','D','E','F'])
     * //result: '41'
     * ```
     * @author danidoble <Daniel Sandoval>
     * @function
     * @param {array} arr Array of numbers in hexadecimal
     * @returns {string}
     */
    boardroidSumHex(arr) {
        let sum = 0;
        arr.forEach((value, index) => {
            if (index !== 0 && index !== 11) {
                sum += parseInt(value, 16);
            }
        });
        return sum.toString(16).toUpperCase();
    }

    /**
     * Checksum of relay hexadecimal numbers
     * ```
     * dd.relaySumHex(['A','B','C','D'])
     * //result: '41'
     * ```
     * @author danidoble <Daniel Sandoval>
     * @function
     * @param {array} arr Array of numbers in hexadecimal
     * @returns {string}
     */
    relaySumHex(arr) {
        let sum = 0;
        arr.forEach((value, index) => {
            if (index !== 3) {
                sum += parseInt(value, 16);
            }
        });
        return sum.toString(16).toUpperCase();
    }

    /**
     * Checksum of jofemar hexadecimal numbers
     * ```
     * dd.jofemarCalcCheckSums('A')
     * //result: ["F0", "F"]
     * ```
     * @author danidoble <Daniel Sandoval>
     * @function
     * @param {string|array} val sum of hexadecimals
     * @returns {array}
     */
    jofemarCalcCheckSums(val) {
        val = this.add0x([this.decToHex(parseInt(val).toString())]);
        let f_ck = [];
        f_ck.push((((val & 0xFF) | 0xF0).toString(16)).toUpperCase());
        f_ck.push((((val & 0xFF) | 0x0F).toString(16)).toUpperCase());
        return f_ck;
    }

