    /**
     * Validate if tray is valid
     * @author danidoble <Daniel Sandoval>
     * @function
     * @param {number|string} tray
     * @returns {{tray: (string), error: boolean}}
     */
    validateTray(tray) {
        let error = true;
        if (typeof tray === "string") {
            tray = dd.hexToDec(tray);
        }
        if (typeof tray === "number") {
            if (tray >= 128) {
                tray -= 128;
            }
            if (tray >= 11 && tray <= 23) {
                error = false;
                tray = dd.decToHex((128 + tray).toString());
            }
        }
        return {error: error, tray: tray};
    }

    /**
     * Validate if channel is valid
     * @author danidoble <Daniel Sandoval>
     * @function
     * @param {number|string} channel
     * @returns {{channel: (string), error: boolean}}
     */
    validateChannel(channel) {
        let error = true;
        if (typeof channel === "string") {
            channel = dd.hexToDec(channel);
        }
        if (typeof channel === "number") {
            if (channel >= 128) {
                channel -= 128;
            }
            if (channel >= 0 && channel <= 9) {
                error = false;
                channel = dd.decToHex((128 + channel).toString());
            }
        }

        return {error: error, channel: channel};
    }


