    /**
     * reset all errors of machine
     * ```
     * jof1.resetAll();
     * ```
     * @author danidoble <Daniel Sandoval>
     * @function
     * @return {void}
     */
    resetAll() {
        let this1 = this;
        if (typeof window.Swal.fire === "function") {
            let timerInterval
            Swal.fire({
                title: dd.__("Reset errors, wait..."),
                html: (dd.__("The window will close automatically en [b] seconds")).replaceAll('[b]', "<b></b>"),
                timer: 21e3,//21 segundos
                timerProgressBar: true,
                allowOutsideClick: false,
                didOpen: () => {
                    setTimeout(function () {
                        this1.reset("80");
                        setTimeout(() => {
                            this1.reset("81");
                            setTimeout(() => {
                                this1.reset("FF");
                            }, 500);
                        }, 500);
                    }, 100);
                    Swal.showLoading()
                    const b = Swal.getHtmlContainer().querySelector('b')
                    timerInterval = setInterval(() => {
                        b.textContent = parseInt((1 + (Swal.getTimerLeft() / 1000)).toString()).toString()
                    }, 1000)
                },
                willClose: () => {
                    clearInterval(timerInterval)
                }
            })
        } else {
            this1.reset("80");
            setTimeout(() => {
                this1.reset("81");
                setTimeout(() => {
                    this1.reset("FF");
                }, 500);
            }, 500);
        }
    }

