    continueMultipleNow() {
        this.#_waiting_withdrawal_timer = 0;
    }

    /**
     * Assign custom waiting for sense timer
     * ```javascript
     * jof1.setCustomWithdrawalTimer(30); // 30 seconds
     * ```
     * @function
     * @param {int} [seconds=60]
     * @author danidoble <Daniel Sandoval>
     * @return void
     */
    setCustomWithdrawalTimer(seconds = 60) {
        if (seconds < 1) {
            seconds = 60;
        }
        this._custom_waiting_withdrawal_timer = seconds * 1e3;
    }

    /**
     * Start the waiting for sense, use this for testing
     * @function
     * @param {number} selection
     * @param {number} id_movement
     * @author danidoble <Daniel Sandoval>
     * @return void
     */
    waitingForSenseTest(selection, id_movement) {
        let this1 = this;
        this1.__sense_selection = selection;
        this1.__sense_id_movement = id_movement;
        this1.#_waiting_for_sense = setInterval(() => {
            if (this1.#_status_dispense === undefined) {
                dd.log("dispensing... #timeout:", this1.#_count_timeout_sense + "/50");
                if (this1.#_count_timeout_sense >= 50) {
                    this1.#_count_timeout_sense = 0;
                    this1.#_status_dispense = false;
                } else {
                    this1.#_count_timeout_sense++;
                }
                this1.extWaitingForSenseTest('undefined');
            } else if (this1.#_status_dispense === true) {
                dd.log("dispensing: " + this1.#_status_dispense);
                this1.clearSenseProduct();

                //user custom
                this1.extWaitingForSenseTest('dispensed');
                setTimeout(() => {
                    this1.softReload();
                }, this1.#_time_to_reload);
            } else if (this1.#_status_dispense === false) {
                this1.clearSenseProduct();

                //user custom
                this1.extWaitingForSenseTest('not-dispensed');
                setTimeout(() => {
                    this1.softReload();
                }, this1.#_time_to_reload);
            }
        }, this1.#_time_interval_sense);
    }

    /**
     * read the current temperature of machine
     * @function
     * @author danidoble <Daniel Sandoval>
     * @return void
     */
    readCurrentTemperature() {
        //02 30 30 81 43 74 FF F9 9F 03
        let f_hex = ['02', '30', '30', (128 + this.#_no_machine).toString(16), '43', '74', 'FF'];
        this.#preSendBytes(f_hex, 'current temperature');
    }

    /**
     * read working temperature of machine
     * @function
     * @author danidoble <Daniel Sandoval>
     * @return void
     */
    readWorkingTemperature() {
        //02 30 30 81 43 - 54 FF F9 7F 03
        let f_hex = ['02', '30', '30', (128 + this.#_no_machine).toString(16), '43', '54', 'FF'];
        this.#preSendBytes(f_hex, 'working temperature');
    }

    /**
     * set the working temperature to machine
     * @function
     * @param {string} temp hexadecimal value
     * @author danidoble <Daniel Sandoval>
     * @return void
     */
    setWorkingTemperature(temp) {
        this.program('54', temp)
        let this1 = this;
        setTimeout(function () {
            this1.readWorkingTemperature();
        }, 2000)
    }

    /**
     * Emulation of response of machine, just for testing
     * @function
     * @author danidoble <Daniel Sandoval>
     * @param {Object} obj
     * @return void
     */
    #emulator(obj) {
        if (typeof obj.code === "object") {
            dd.warn(dd.__("Executing emulator be carefully"));
            if (!this.#_connected) {
                this.#_connected = true;
                window["owl_jof" + this.#_no_machine + "_connected"] = this.#_connected;
                if (this.#_interval_reconnection !== undefined) {
                    clearInterval(this.#_interval_reconnection);
                    this.#_interval_reconnection = undefined;
                }
            }
            clearTimeout(this.#_timer_until_response);
            this.#_timer_until_response = undefined;
            this.#serialMessage(obj.code);
        } else {
            dd.error(dd.__("Invalid data to emulate"));
        }
    }

