    softReload() {
        this.#_interval_reconnection = this.#_const_original_values._interval_reconnection;
        this.#_timer_until_response = this.#_const_original_values._timer_until_response;
        this.#_status_dispense = this.#_const_original_values._status_dispense;
        this.#_time_response_general = this.#_const_original_values._time_response_general;
        this.#_time_to_reconnect = this.#_const_original_values._time_to_reconnect;
        this.#_last_error = this.#_const_original_values._last_error;

        this._log_time_door_open = [{
            "name": "open",
            "listen_since": (new Date()).getTime(),
            "listen_until": ((new Date()).getTime())
        }];
        this._log_time_door_close = [{
            "name": "close",
            "listen_since": (new Date()).getTime(),
            "listen_until": ((new Date()).getTime())
        }];
        this._counter_sense_door = 1;
        this._interval_sense_door = undefined;
        this._limit_sense_door = 30;

        /**
         * Call another function to clean variables used in each project without modify the source
         */
        this.extSoftReload();
    }

