    #deviceConnected(event1) {
        let this1 = this;
        this.#_connected = true;
        window["owl_relay" + this.#_no_machine + "_connected"] = this.#_connected;
        this.#_serial_port.ondisconnect = function (event) {
            this1.#deviceDisconnected(event);
        };
        this.#_serial_port.onconnect = function (event) {
            this1.#deviceConnected(event);
        };
        dd.log(event1);
    }

