    /**
     * Set On relay (this means turn off or turn on power depends on how is connected)
     * ```
     * relay1.setOn()
     * // or
     * relay1.setOn(2)
     * ```
     * @author danidoble <Daniel Sandoval>
     * @function
     * @param {number}[no=1] number of relay to activate
     * @return void
     */
    setOn(no = 1) {
        let activate = this.#_codes.activate;
        activate[1] = dd.hexMaker(dd.decToHex((no).toString()));
        activate[3] = dd.relaySumHex(activate);
        this.#preSendBytes(activate, 'activate');
    }
    /**
     * Set Off relay (this means turn off or turn on power depends on how is connected)
     * ```
     * relay1.setOff()
     * // or
     * relay1.setOff(2)
     * ```
     * @author danidoble <Daniel Sandoval>
     * @function
     * @param {number}[no=1] number of relay to deactivate
     * @return void
     */
    setOff(no = 1) {
        let deactivate = this.#_codes.deactivate;
        deactivate[1] = dd.hexMaker(dd.decToHex((no).toString()));
        deactivate[3] = dd.relaySumHex(deactivate);
        this.#preSendBytes(deactivate, 'deactivate');
    }

    /**
     * Set Off relay and after set On
     * ```
     * relay1.toggle()
     * // or
     * relay1.toggle(true)
     * ```
     * @author danidoble <Daniel Sandoval>
     * @function
     * @param {boolean}[inverse=false] invert order to do toggle
     * @return void
     */
    toggle(inverse = false) {
        let this1 = this;
        if(!inverse){
            this1.setOn();
            setTimeout(()=>{
                this1.setOff();
            },3e2);
        }else{
            this1.setOff();
            setTimeout(()=>{
                this1.setOn();
            },3e2);
        }
    }

