    static productNotDispensed(instance = null) {
        if (Emulate.#checkInstance(instance) === false || Emulate.#withoutLocker()) {
            return;
        }
        switch (Emulate.#_instance_type) {
            case "Boardroid":
                Emulate.#do(['2', 'D7', 'A', '0', '0', '1', '0', '0', '0', '0', '0', '0', 'F2']);
                break;
            case 'Jofemar':
                Emulate.#do(['6', '34']); // empty channel
                break;
        }
    }

